@media (max-width: 48em) {
  .footer .container {
    padding: 0;
  }
  .footer__title {
    font-size: 4rem;
    margin-bottom: 5.4rem;
  }
  .footer__top {
    display: flex;
    flex-direction: column;
    padding: 0;
  }
  .footer__top-left {
    border-bottom: 1px solid #c7c7c7;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-bottom: 4rem;
  }
  .footer__top-logo {
    width: 28rem;
    height: 7.6rem;
    margin-bottom: 4.8rem;
  }
  .footer__top-address {
    width: 100%;
    font-size: 2.4rem;
    font-style: normal;
    line-height: 140%;
  }
  .footer__top-center {
    padding-top: 6rem;
    padding-left: 2rem;
    padding-right: 2rem;
  }
  .footer__top-lists {
    grid-template-columns: 1fr;
    gap: 3rem;
  }
  .footer__top-lists li {
    font-size: 2.4rem;
  }
  .footer__top-right {
    padding-top: 8rem;
    padding-bottom: 6.4rem;
  }
  .footer__top-right .footer__top-lists {
    grid-template-columns: 1fr 1fr;
  }
  .footer__bottom {
    flex-direction: column;
    padding-top: 5.6rem;
    padding-bottom: 3.2rem;
  }
  .footer__bottom-date {
    font-size: 2.4rem;
    margin-bottom: 5.6rem;
  }
  .footer__bottom-policy {
    font-size: 2.4rem;
    line-height: 140%;
    margin-bottom: 5.6rem;
    text-align: center;
  }
  .footer__bottom-logo {
    width: 23rem;
    height: 5.8rem;
  }
